import Layout from '../components/layout'

const Skills = () => (
    <Layout>
        <h1>My Skills</h1>
        <div className="services">
            <div className="services-list">
                <div className="item">
                    <img src="/static/services/weddings.jpg"/>
                    <h2>Mobile</h2>
                    <p>Android(Kotlin), iOS(Swift), Cross-platform(React Native).</p>
                </div>

                <div className="item">
                    <img src="/static/services/portraits.jpg"/>
                    <h2>Web</h2>
                    <p>HTML5, JavaScript, CSS, TypeScript, Bootstrap, Angular, React, Vue.</p>
                </div>

                <div className="item">
                    <img src="/static/services/real-estate.jpg"/>
                    <h2>Server</h2>
                    <p
                    >MySQL, MariaDB, MongoDB, Reddis.<br/>
                    Nginx + PHP + CodeIgniter, Node.js + Express.<br/>
                    AppRTC, Openfire, Socket.io.<br/>
                    AWS, AliYun, Docker/Docker Compose..
                    </p>
                </div>

                <div className="item">
                    <img src="/static/services/travel.jpg"/>
                    <h2>Others</h2>
                    <p>
                    WebRTC based audio / video chatting.<br/>
                    XMPP based text chatting.<br/>
                    Facebook, Twitter, YouTube, WeChat SNS integration
                    </p>
                </div>
            </div>
        </div>
        <style jsx>{`
            .services {
                max-width: 1000px;
                margin: 0 auto;
            }
            .services-list {
                padding: 0 30px;
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
            }
            .services .item {
                padding: 20px;
                margin-bottom: 40px;
                width: 50%;
            }
            img {
                max-width: 100%;
            }
            h2 {
                margin: 0 0 5px 0;
            }
            p {
                font-size: 18px;
                color: #777;
            }
            @media (max-width: 600px) {
                .services .item {
                    width: auto;
                    padding: 10px 20px;
                }
            }
        `}</style>
    </Layout>
);

export default Skills;